:root {
  --accent-color: #00acdc;
}

body, .nav__list {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

.img {
  width: 40px;
  height: 40px;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.nav {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.nav__item {
  height: 60px;
  margin: 0 10px;
  list-style-type: none;
  display: flex;
  position: relative;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  align-items: center;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  text-decoration: none;
  display: flex;
}

.nav__link:hover, .is-active {
  color: var(--accent-color);
}

.nav__list {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--accent-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__link {
  align-items: center;
  height: 60px;
  display: flex;
}
